.faq-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 85px 50px 0 50px;
  .word {
    display: flex;
    width: min-content;
    margin-bottom: 50px;
    .letter {
      text-align: center;
      margin: 0 15px;
      padding: 10px 25px;

      color: #EBEBEB;
      font-size: 60px;
      background: #413F3F;
      border-radius: 25px;
      @media only screen and (max-width: 550px) {
        border-radius: 20px;
        font-size: 50px;
        margin: 0px 7px;
        padding: 8px 20px;
      }
    }
  }

  .MuiAccordion-root:before {
    border: none;
  }
}